@import url('https://fonts.googleapis.com/css?family=Audiowide');

* {
    margin: 0;
    padding: 0;
}

body {
    width: 100vw;
    height: 100vh;
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: 'Audiowide';
}

.hubPage {
    position: relative;
    width: 100%;
    height: 100%;
}

.hub-body {
    display: inline-flex;
    justify-content: space-evenly;
    align-items: stretch;
}

.hub-menu {
    display: inline-flex;
    justify-content: space-evenly;
    position: relative;
    float: right;
    width: 80vw;
    height: auto;
}

.hub-left, .hub-right {
    display: inline-block;
    min-width: 250px;
    width: 33vw;
    height: 90vh;
}

@media screen and  (max-width: 800px){
    .hub-menu {
        display: inline-block;
    }

    .hub-left, .hub-right {
        margin-left: 5%;
        margin-right: 5%;
        width: 90%;

    }

    .hub-right {
        padding-bottom: 5%;
    }
}